.view {
	padding: 108px 0 124px;
	@include d {
		padding: 85px 0 90px; }
	@include m {
		padding: 75px 0 85px; }
	&__center {
		max-width: 1228px; }
	&__title {
		margin-bottom: 35px;
		text-align: center;
		@include d {
			margin-bottom: 25px; }
		@include m {
			text-align: left; } }
	&__info {
		max-width: 822px;
		margin: 0 auto 70px;
		text-align: center;
		@include m {
			margin-bottom: 40px;
			text-align: left; } }
	&__video {
		@include shadow4; }
	.video {
		border-radius: 15px; } }

@include dark {
	.view {
		&__video {
			@include shadow4-dark; } } }

