.main5 {
	position: relative;
	padding: 0 0 124px 0;
	@include d {
		padding: 105px 0; }
	@include t {
		padding: 90px 0; }
	@include m {
		padding: 70px 0; }
	&__row {
		display: flex;
		align-items: center;
		margin: 0 -15px;
		@include m {
			display: block;
			margin: 0; } }
	&__col {
		flex: 0 0 calc(50% - 30px);
		width: calc(50% - 30px);
		margin: 0 15px;
		@include m {
			width: 100%;
			margin: 0;
			&:first-child {
				margin-bottom: 60px; } } }
	&__preview {
		position: relative;
		// +shadow4
		// overflow: hidden
		img {
			border-radius: 15px;
			width: 100%; } }
	&__play {
		top: 46px;
		left: 44px;
		@include t {
			top: 25px;
			left: 25px; } }
	&__col:first-child &__wrap {
		padding-right: 105px;
		@include w {
			padding-right: 60px; }
		@include d {
			padding-right: 40px; }
		@include t {
			padding-right: 0; } }
	&__col:nth-child(2) &__wrap {
		padding-left: 105px;
		@include w {
			padding-left: 60px; }
		@include d {
			padding-left: 40px; }
		@include t {
			padding-left: 0; } }
	&__title {
		margin-bottom: 31px;
		@include t {
			margin-bottom: 25px; } }
	&__info {
		@include nl {
			margin-bottom: 29px; } } }

@include dark {
	.main5 {
		&__preview {
			@include shadow4-dark; } } }

