.quality3 {
	padding: 108px 0 119px;
	@include d {
		padding: 90px 0; }
	@include m {
		padding: 75px 0 86px; }
	&__title {
		margin-bottom: 35px;
		text-align: center;
		@include d {
			margin-bottom: 25px; }
		@include m {
			text-align: left; }
		&_mb75 {
			margin-bottom: 75px;
			@include t {
				margin-bottom: 55px; } }
		&_big {
			@include d {
				font-size: 48px; }
			@include m {
				font-size: 40px; } } }
	&__info {
		max-width: 780px;
		margin: 0 auto 83px;
		text-align: center;
		@include d {
			max-width: 690px;
			margin-bottom: 40px; }
		@include m {
			max-width: 100%;
			text-align: left; } }
	&__list {
		display: flex;
		flex-wrap: wrap;
		margin: -42px -16px 0;
		@include d {
			margin: -30px -11px 0; }
		@include m {
			display: block;
			margin: 0; } }
	&__item {
		flex: 0 0 calc(33.33% - 32px);
		width: calc(33.33% - 32px);
		margin: 42px 16px 0;
		padding: 32px;
		border-radius: 15px;
		background: $white;
		transition: box-shadow .2s;
		@include d {
			flex: 0 0 calc(50% - 22px);
			width: calc(50% - 22px);
			margin: 32px 11px 0; }
		@include m {
			width: 100%;
			margin: 0; }
		@include nl {
			@include m {
				margin-bottom: 28px; } }
		&:hover {
			@include shadow4; } }
	&__icon {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 80px;
		height: 80px;
		margin-bottom: 29px;
		border-radius: 50%; }
	&__category {
		margin-bottom: 21px;
		@include h6;
		font-weight: 700;
		@include m {
			margin-bottom: 15px;
			line-height: (40/28); } }
	&__text {
		color: $text3; }
	.more {
		margin-top: 16px; } }

@include dark {
	.quality3 {
		&__item {
			background: $dark2;
			&:hover {
				@include shadow4-dark; } } } }
