.main3 {
	position: relative;
	padding-top: 106px;
	@include t {
		padding-top: 90px; }
	@include m {
		padding: 62px 0 57px; }
	&:before {
		content: "";
		position: absolute;
		left: 0;
		right: 0;
		bottom: 0;
		height: 408px;
		z-index: -1;
		background: $bg3;
		@include d {
			height: 274px; }
		@include m {
			height: 144px; } }
	&__title {
		max-width: 790px;
		margin: 0 auto 32px;
		text-align: center;
		@include m {
			margin-bottom: 24px;
			text-align: left;
			line-height: 1.1; } }
	&__info {
		max-width: 860px;
		margin: 0 auto 42px;
		text-align: center;
		@include m {
			margin-bottom: 25px;
			text-align: left; } }
	&__btns {
		display: flex;
		justify-content: center;
		@include m {
			display: block;
			margin-bottom: 60px; } }
	&__btn {
		@include m {
			min-width: 100%; }
		@include nl {
			margin-right: 32px;
			@include m {
				margin: 0 0 25px; } } }
	&__preview {
		position: relative;
		left: 50%;
		width: 1440px;
		transform: translateX(-50%);
		@include d {
			width: 1192px; }
		@include t {
			width: 900px; }
		@include m {
			width: 461px; }
		img {
			width: 100%; } } }

@include dark {
	.main3 {
		&:before {
			background: $dark3; } } }
