.main2 {
	position: relative;
	@include t {
		padding: 70px 0 80px; }
	@include m {
		padding: 50px 0 65px; }
	&__center {
		display: flex;
		align-items: center;
		min-height: 1113px;
		padding-top: 53px;
		padding-bottom: 105px;
		@include w {
			min-height: 1000px; }
		@include d {
			min-height: 700px; }
		@include t {
			display: block;
			min-height: auto;
			padding-top: 0;
			padding-bottom: 0; } }
	&__wrap {
		position: relative;
		z-index: 3;
		max-width: 495px;
		@include x {
			max-width: 450px; }
		@include d {
			max-width: 420px; }
		@include t {
			max-width: 100%; } }
	&__title {
		margin-bottom: 35px;
		@include m {
			margin-bottom: 25px; } }
	&__info {
		margin-bottom: 36px; }
	&__btns {
		display: flex;
		margin-bottom: 63px;
		@include d {
			margin-bottom: 50px; }
		@include m {
			display: block;
			margin-bottom: 40px; } }
	&__line {
		display: flex;
		align-items: center; }
	&__social {
		display: flex;
		align-items: center;
		@include nl {
			margin-right: 32px;
			@include m {
				margin-right: 20px; } } }
	&__link {
		display: flex;
		justify-content: center;
		align-items: center;
		flex-shrink: 0;
		width: 72px;
		height: 72px;
		border-radius: 50%;
		background: #F5F6FA;
		transition: background .2s;
		@include m {
			width: 60px;
			height: 60px; }
		&:hover {
			background: $bg2; }
		img {
			max-width: 35px;
			max-height: 35px;
			@include m {
				max-width: 28px;
				max-height: 28px; } }
		@include nl {
			margin-right: 32px;
			@include m {
				margin-right: 10px; } } }
	&__note {
		color: $text3; }
	&__bg {
		position: absolute;
		top: 50%;
		right: calc(50% - 667px);
		width: 718px;
		z-index: -1;
		transform: translateY(-52.5%);
		@include x {
			right: calc(50% - 650px);
			width: 670px; }
		@include d {
			right: calc(50% - 520px);
			width: 470px; }
		@include t {
			position: relative;
			top: auto;
			right: auto;
			transform: translateY(0);
			margin: 0 auto 50px; }
		@include a {
			width: auto;
			margin: 0 -9px 60px -18px; } }
	&__preview {
		&:nth-child(2) {
			left: 20%;
			bottom: 4%;
			width: 63%;
			@include shadow4;
			@include m {
				left: 13.7%;
				bottom: -4%;
				width: 75%; } }
		img {
			width: 100%; }
		@include nf {
			position: absolute;
			@include shadow4; } } }

@include dark {
	.main2 {
		&__preview {
			@include nf {
				@include shadow4-dark; } }
		&__link {
			background: $dark3;
			&:hover {
				background: $dark2; } } } }
