body {
  min-width: 375px;
  @include font-family;
  font-size: 18px;
  line-height: 1.35;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: $dark;
  @include d {
    font-size: 20px;
    line-height: 1.25; }
  &.no-scroll {
    overflow: hidden; } }

button,
input,
textarea,
select {
  @include font-family; }

.page {
  overflow: hidden; }

.center {
  max-width: 1384px;
  margin: 0 auto;
  padding: 0 107px;
  @include d {
    padding: 0 100px; }
  @include t {
    padding: 0 50px; }
  @include m {
    padding: 0 32px; } }

.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
  font-weight: 700; }

.h1,
.h2,
.h3 {
  letter-spacing: -.5px; }

.h1 {
  font-size: 76px;
  line-height: (92/76); }

.h2 {
  font-size: 64px;
  line-height: (72/64);
  @include d {
    font-size: 48px;
    line-height: (56/48); }
  @include m {
    font-size: 40px;
    line-height: (48/40); } }

.h3 {
  font-size: 48px;
  line-height: (56/48); }

.h4 {
  font-size: 40px;
  line-height: (48/40);
  letter-spacing: -.4px;
  @include d {
    font-size: 32px;
    line-height: (40/32);
    letter-spacing: -.5px; }
  @include m {
    font-size: 24px;
    line-height: (32/24);
    letter-spacing: -.2px; } }

.h5 {
  font-size: 32px;
  line-height: (40/32);
  letter-spacing: -.5px;
  @include d {
    font-size: 24px;
    line-height: (32/24);
    letter-spacing: -.2px; } }

.h6 {
  font-size: 24px;
  line-height: (32/24);
  letter-spacing: -.2px;
  @include d {
    font-size: 18px;
    line-height: (24/18); } }

.btn {
  position: relative;
  min-width: 180px;
  height: 64px;
  border-radius: 8px;
  @include font-family;
  border-width: 2px;
  border-style: solid;
  overflow: hidden;
  font-size: 18px;
  font-weight: 700;
  transition: all .35s;
  @include d {
    height: 56px;
    font-size: 16px; }
  @include m {
    min-width: 160px; }
  &:before {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: -1;
    height: 300%;
    border-radius: 50% 50% 0 0/75% 75% 0 0;
    transition: height .35s; }
  &:hover:before {
    height: 0; }
  &:hover {
    transform: translateY(-2px); }
  &:active {
    transform: translateY(2px); }
  &_secondary {
    border-color: $secondary;
    color: $wh;
    &:before {
      background: $secondary; }
    &:hover {
      color: $secondary; } }
  &_outline {
    border-color: $dark;
    color: $dark;
    &:before {
      background: $wh; }
    &:hover {
      color: $dark; } }
  &_dark {
    border-color: $dark;
    color: $wh;
    &:before {
      background: $dark; }
    &:hover {
      color: $dark; } }
  &_primary {
    border-color: $primary;
    color: $wh;
    &:before {
      background: $primary; }
    &:hover {
      color: $primary; } }
  &_white {
    border-color: $wh;
    color: $primary;
    &:before {
      background: $wh; }
    &:hover {
      color: $wh; } }
  [href] {
    display: inline-flex;
    justify-content: center;
    align-items: center; } }

.header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 10px;
  padding: 64px 0;
  @include d {
    padding: 40px 0; }
  &__center {
    display: flex;
    align-items: center;
    width: 100%;
    max-width: 100%;
    padding: 0 72px;
    @include d {
      padding: 0 50px; }
    @include t {
      padding: 0 30px; } }
  &__logo {
    font-size: 0;
    @include m {
      position: relative;
      z-index: 15;
      margin-right: auto;
      &.toggle {
        .header__pic_black {
          display: none; }
        .header__pic_white {
          display: inline-block; } } } }
  &__pic {
    width: 214px;
    @include d {
      width: 161px; }
    &_white {
      display: none; } }
  &__wrap {
    display: flex;
    flex-grow: 1;
    padding-left: 20px;
    @include t {
      padding: 0; }
    @include m {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      z-index: 10;
      flex-direction: column;
      align-items: flex-start;
      min-height: 100vh;
      padding: 150px 32px 40px;
      background: $dark;
      transform: translateX(-100%);
      transition: transform .25s;
      &.visible {
        transform: translateX(0);
        .line {
          path {
            animation: dash 1.5s linear forwards; } } } } }
  &__nav {
    display: flex;
    margin: 0 auto;
    @include m {
      flex-direction: column;
      align-items: flex-start;
      margin: 0 0 auto; } }
  &__item {
    position: relative;
    font-weight: 600;
    color: $text-dark;
    transition: color .25s;
    @include m {
      font-size: 32px;
      color: #595976; }
    &:hover {
      color: $text-light;
      @include m {
        color: #595976; } }
    &.active {
      color: $primary;
      @include m {
        color: $wh; } }
    @include nl {
      margin-right: 68px;
      @include d {
        margin-right: 54px; }
      @include t {
        margin-right: 40px; }
      @include m {
        margin: 0 0 32px; }
      @include a {
        margin-bottom: 40px; } } }
  &__counter {
    position: absolute;
    left: calc(100% + 7px);
    bottom: calc(100% - 11px);
    min-width: 24px;
    padding: 0 5px;
    border-radius: 50%;
    // background: linear-gradient(180deg, #3A8FFF 0%, #0837AE 100%)
    background-color: $pink;
    text-align: center;
    font-size: 14px;
    line-height: 24px;
    font-weight: 600;
    color: $wh;
    @include m {
      bottom: calc(100% - 17px); } }
  &__btn {
    // +w
    //   font-size: 18px
    //   height: 56px
    @include t {
      min-width: 130px;
      height: 46px; }
    @include m {
      display: none; } }
  &__burger {
    position: relative;
    z-index: 15;
    display: none;
    width: 32px;
    height: 32px;
    text-align: center;
    transition: background .3s;
    -webkit-tap-highlight-color: rgba(0,0,0,0);
    @include m {
      display: inline-block; }
    &:before,
    &:after {
      content: '';
      display: block;
      width: 32px;
      height: 2px;
      background: $dark;
      transition: transform .3s, width .3s, background .3s; }
    &:before {
      margin-bottom: 8px; }
    &.active {
      &:before,
      &:after {
        background: $wh; }
      &:before {
        transform: translateY(5px) rotate(45deg); }
      &:after {
        transform: translateY(-5px) rotate(-45deg); } } }
  &__social {
    display: none;
    @include m {
      display: flex;
      margin-top: 60px; } }
  &__link {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    border: 1px solid rgba(#D8D8D8, .2);
    font-size: 0;
    .icon {
      font-size: 12px;
      &-youtube {
        font-size: 10px;
        fill: #ADADAD; } }
    @include nl {
      margin-right: 32px; } }
  &__bg {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 5;
    background: rgba(#000, .7);
    visibility: hidden;
    opacity: 0;
    transition: all .25s;
    &.show {
      @include m {
        visibility: visible;
        opacity: 1; } } }
  .line {
    display: none;
    @include m {
      display: block;
      top: -152px;
      left: 0; } } }

.title {
  font-size: 80px;
  font-weight: 700;
  line-height: (100/79);
  letter-spacing: -1px;
  @include d {
    font-size: 60px;
    line-height: (72/60);
    letter-spacing: -.5px; }
  @include m {
    font-size: 44px;
    line-height: (56/44); }
  &__color {
    color: $primary; }
  &_big {
    font-size: 110px;
    line-height: (131/110);
    letter-spacing: -1.2px;
    @include d {
      font-size: 76px;
      line-height: (92/76);
      letter-spacing: -.5px; } } }

.main {
  position: relative;
  padding: 238px 0 120px;
  @include d {
    padding: 169px 0 115px; }
  @include m {
    padding: 118px 0 330px; }
  &__wrap {
    max-width: 470px;
    @include m {
      max-width: 100%; } }
  &__title {
    margin-bottom: 40px;
    @include d {
      margin-bottom: 24px; }
    p {
      display: inline-flex;
      overflow: hidden;
      span {
        display: block;
        @include nl {
          margin-right: 18px; } } }
    &[data-aos] {
      p {
        span {
          transform: translateY(105%);
          transition: transform .8s; } }
      &.aos-animate {
        p {
          span {
            transform: translateY(0); } }
        p:first-child {
          span {
            &:first-child {
              transition-delay: .2s; }
            &:nth-child(2) {
              transition-delay: .4s; } } }
        p:nth-child(2) {
          span {
            &:first-child {
              transition-delay: .6s; }
            &:nth-child(2) {
              transition-delay: .8s; } } }
        p:nth-child(3) {
          span {
            &:first-child {
              transition-delay: .8s; } } } } } }
  &__info {
    max-width: 370px;
    margin-bottom: 48px;
    line-height: (32/18);
    color: $text-light;
    @include d {
      max-width: 265px;
      margin-bottom: 32px; }
    @include m {
      max-width: 100%;
      margin-bottom: 24px; } }
  &__btn {
    margin-bottom: 56px;
    @include d {
      margin-bottom: 56px; }
    @include t {
      margin-bottom: 32px; } }
  &__scroll {
    display: inline-flex;
    align-items: center;
    font-weight: 600;
    color: $text-dark;
    @include m {
      display: none; } }
  &__icon {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;
    width: 48px;
    height: 48px;
    margin-right: 16px;
    border-radius: 50%;
    background: $secondary;
    overflow: hidden;
    font-size: 0;
    @include d {
      width: 40px;
      height: 40px; } }
  &__arrows {
    display: flex;
    flex-direction: column;
    transform: translateY(-43px);
    transition: transform .35s; }
  &__arrows &__pic {
    @include nl {
      margin-bottom: 64px; } }
  &__scroll:hover &__arrows {
    animation: arrowsDown .5s forwards; }
  &__bg {
    position: absolute;
    top: 283px;
    right: calc(50% - 691px);
    z-index: -1;
    width: 928px;
    font-size: 0;
    @include d {
      top: 210px;
      right: calc(50% - 453px);
      width: 700px; }
    @include t {
      top: 230px;
      width: 600px; }
    @include m {
      top: auto;
      right: calc(50% - 203px);
      bottom: 56px;
      width: 400px; }
    &[data-aos] {
      .main__preview {
        opacity: 0;
        transition: all .6s;
        &:nth-child(1) {
          transform: translateY(60px); }
        &:nth-child(2) {
          transform: translateX(80px);
          transition-delay: .2s; }
        &:nth-child(3) {
          transform: translateX(40px);
          transition-delay: .4s; }
        &:nth-child(4) {
          transform: translateY(60px);
          transition-delay: .6s; }
        &:nth-child(5) {
          transform: translateX(50px);
          transition-delay: .8s; }
        &:nth-child(6) {
          transform: translateX(50px) skewX(10deg);
          transition-delay: 1s; }
        &:nth-child(7) {
          transform: translateX(80px);
          transition-delay: .6s; } }
      &.aos-animate {
        .main__preview {
          opacity: 1;
          &:first-child {
            transform: translateY(-60px);
            @include d {
              transform: translateY(-40px); } }
          &:nth-child(2) {
            transform: translateY(-40px) scaleY(1)  translateX(0); }
          &:nth-child(3) {
            transform: translateY(-40px)  translateX(0); }
          &:nth-child(4) {
            transform: translateY(0); }
          &:nth-child(5) {
            transform: translateX(0); }
          &:nth-child(6) {
            transform: translateX(0) skewX(0); }
          &:nth-child(7) {
            transform: translateY(-40px) scaleY(1)  translateX(0); } } } } }
  &__bg &__pic {
    width: 100%; }
  &__preview {
    &:first-child {
      position: relative;
      z-index: 3; }
    &:nth-child(2) {
      top: 0;
      z-index: 4; }
    &:nth-child(3) {
      top: 0;
      z-index: 5; }
    &:nth-child(4) {
      top: 50.5%;
      left: 12.8%;
      z-index: 5;
      width: 40.7%; }
    &:nth-child(5) {
      top: -9.4%;
      right: 5.8%;
      z-index: 1;
      width: 60.7%;
      @include m {
        right: -2%; } }
    &:nth-child(6) {
      top: 5.5%;
      right: 12%;
      z-index: 2;
      width: 27.5%;
      @include m {
        top: 10%;
        right: 8%; } }
    &:nth-child(7) {
      top: 0;
      z-index: 4; }
    @include nf {
      position: absolute; } }
  & > .line {
    top: 0;
    left: calc(50% - 395px);
    @include d {
      top: -110px;
      left: calc(50% - 365px); }
    @include t {
      left: 100px; }
    @include m {
      top: -160px;
      left: 75px; }
    path {
      animation: dash 2.5s .8s linear forwards; } } }

.line {
  position: absolute;
  z-index: -5;
  pointer-events: none;
  font-size: 0;
  @include d {
    transform: scale(.75); }
  @include m {
    transform: scale(.63); }
  path {
    stroke-dasharray: 2173;
    stroke-dashoffset: 2173;
    @include d {
      width: 456px; } } }

[data-aos="animation-line"] {
  path {
    animation: none; }
  &.aos-animate {
    path {
      animation: dash 3s linear forwards; } } }

@keyframes dash {
  to {
    stroke-dashoffset: 0; } }

@keyframes arrowsDown {
  100% {
    transform: translateY(43px); } }


.bg1 {
	background: $bg3; }


