    background: $wh;
    opacity: 0;
    transition: all .25s;
    pointer-events: none;
    @include d {
      left: calc(100% + 5px); }
    &:before {
      content: "";
      position: absolute;
      top: -2px;
      left: -2px;
      width: 5px;
