.quality1 {
	padding: 114px 0 113px;
	@include d {
		padding: 90px 0; }
	&__list {
		display: flex;
		flex-wrap: wrap;
		margin: 0 -16px;
		@include t {
			display: block;
			margin: 0; } }
	&__item {
		flex: 0 0 calc(33.33% - 32px);
		width: calc(33.33% - 32px);
		margin: 0 16px;
		padding: 32px;
		border-radius: 15px;
		background: $white;
		transition: box-shadow .2s;
		@include d {
			flex: 0 0 calc(50% - 32px);
			width: calc(50% - 32px); }
		@include t {
			width: 100%;
			margin: 0; }
		&:nth-child(n+3) {
			@include d {
				display: none; }
			@include t {
				display: block; } }
		@include nl {
			@include t {
				margin-bottom: 25px; } }
		&:hover {
			@include shadow4; } }
	&__icon {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 80px;
		height: 80px;
		margin-bottom: 29px;
		border-radius: 50%; }
	&__category {
		margin-bottom: 21px;
		@include h6;
		font-weight: 700; }
	&__text {
		color: $text3; } }

@include dark {
	.quality1 {
		&__item {
			background: $dark2;
			&:hover {
				@include shadow4-dark; } } } }
