$aubergine: #4A154B;
$purple: $aubergine;
$text-dark: #8B8B8B;
$text-light: #606060;
$text-white: #BBBBBB;
$pink: rgb(228, 48, 126);

$gray-light: #ECF2F6;
$yellow: #ECB22E;
$red: #E01E5A;
$green: #53B175;
$blue: #5468E7;

$primary: $blue;
$secondary: $green;


@mixin font-family {
  // font-family: 'Poppins', sans-serif
  font-family: 'SF Pro Display', sans-serif; }

