@mixin shadow4 {
	box-shadow: 0px 50px 77px rgba(176, 183, 195, 0.22); }

@mixin shadow4-dark {
	box-shadow: 0px 50px 77px rgba(14, 15, 17, 0.63); }

@mixin dark {
  body.dark {
    @content; } }

$text1: #323B4B;
$text2: #4E5D78;
$text3: #8A94A6;
$text4: #B0B7C3;

$bg1: #C1C7D0;
$bg2: #F3F3F3;
$bg3: #FAFBFC;


$dark1: #383A43;
$dark2: #2A2C34;
$dark3: #1E2029;
$dark4: #12141D;

@mixin h1 {
	font-size: 76px {
  line-height: (92/76); } }

@mixin h2 {
	font-size: 64px {
  line-height: (72/64);
  // +d
  //   font-size: 48px
  //   line-height: (56/48)
  // +m
  //   font-size: 40px
 } }  //   line-height: (48/40)

@mixin h3 {
	font-size: 48px {
  line-height: (56/48); } }

@mixin h4 {
	font-size: 40px {
  line-height: (48/40);
  letter-spacing: -.4px;
  // +d
  //   font-size: 32px
  //   line-height: (40/32)
  //   letter-spacing: -.5px
  // +m
  //   font-size: 24px
  //   line-height: (32/24)
 } }  //   letter-spacing: -.2px

@mixin h5 {
	font-size: 32px {
  line-height: (40/32);
  letter-spacing: -.5px;
  // +d
  //   font-size: 24px
  //   line-height: (32/24)
 } }  //   letter-spacing: -.2px

@mixin h6 {
	font-size: 24px {
  line-height: (32/24);
  letter-spacing: -.2px;
  // +d
  //   font-size: 18px
 } }  //   line-height: (24/18)

@mixin h7 {
	font-size: 26px;
	line-height: (31/26); }

@mixin h8 {
	font-size: 24px;
	line-height: (29/24); }

