.main4 {
	position: relative;
	overflow: hidden;
	@include m {
		padding-top: 60px; }
	&__center {
		display: flex;
		align-items: center;
		min-height: 909px;
		padding-top: 100px;
		padding-bottom: 100px;
		@include d {
			min-height: 783px;
			padding-bottom: 50px; }
		@include t {
			min-height: 700px; }
		@include m {
			display: block;
			min-height: auto;
			padding: 0 20px; } }
	&__wrap {
		position: relative;
		z-index: 3;
		max-width: 570px;
		@include x {
			max-width: 530px; }
		@include d {
			max-width: 350px; } }
	&__title {
		margin-bottom: 31px;
		@include m {
			margin-bottom: 25px; } }
	&__info {
		margin-bottom: 42px; }
	&__btns {
		display: flex;
		@include d {
			display: block; } }
	&__btn {
		@include m {
			min-width: 100%; }
		@include nl {
			margin-right: 32px;
			@include d {
				margin: 0 0 25px; } } }
	&__preview {
		position: absolute;
		right: 0;
		bottom: 0;
		pointer-events: none;
		@include shadow4;
		@include rmin(1580) {
			right: calc(50% - 700px); }
		@include d {
			width: 502px; }
		@include t {
			right: -80px;
			width: 450px; }
		@include m {
			position: static;
			width: auto;
			margin: 66px -20px 0 0; }
		img {
			width: 100%; } } }

@include dark {
	.main4 {
		&__preview {
			@include shadow4-dark; } } }
