.quality4 {
	padding: 121px 0 136px;
	@include d {
		padding: 100px 0 118px; }
	@include m {
		padding: 90px 0 100px; }
	&__title {
		max-width: 880px;
		margin: 0 auto 20px;
		text-align: center;
		@include d {
			max-width: 540px; }
		@include m {
			max-width: 100%;
			text-align: left; } }
	&__info {
		max-width: 820px;
		margin: 0 auto 71px;
		text-align: center;
		@include d {
			max-width: 690px; }
		@include m {
			max-width: 100%;
			margin-bottom: 40px;
			text-align: left; } }
	&__row {
		display: flex;
		margin: 0 -15px;
		@include d {
			margin: 0 -10px; }
		@include m {
			display: block;
			margin: 0; } }
	&__col {
		flex: 0 0 calc(50% - 30px);
		width: calc(50% - 30px);
		margin: 0 15px;
		@include d {
			flex: 0 0 calc(50% - 20px);
			width: calc(50% - 20px);
			margin: 0 10px; }
		@include m {
			width: 100%;
			margin: 0; }
		&:nth-child(2) {
			padding-top: 63px;
			@include d {
				padding-top: 26px; }
			@include m {
				padding: 0; } }
		@include nl {
			@include m {
				margin-bottom: 26px; } } }
	&__item {
		padding: 48px 48px 50px;
		border-radius: 15px;
		background: $bg3;
		@include x {
			padding: 40px 32px; }
		@include d {
			padding: 38px 30px 30px; }
		@include m {
			padding: 38px 32px; }
		@include nl {
			margin-bottom: 30px;
			@include d {
				margin-bottom: 26px; } } }
	&__preview {
		margin-bottom: 49px;
		@include t {
			margin-bottom: 36px; }
		img {
			width: 100%; } }
	&__category {
		display: flex;
		align-items: flex-start;
		margin-bottom: 21px;
		@include h6;
		font-weight: 700;
		@include d {
			font-size: 24px; }
		@include m {
			margin-bottom: 16px; } }
	&__mark {
		flex-shrink: 0;
		margin-left: auto;
		padding: 0 15px;
		background: $primary;
		border-radius: 18px;
		font-size: 18px;
		line-height: 36px;
		font-weight: 600;
		color: $white;
		@include d {
			margin-top: -2px; }
		@include m {
			margin-left: 15px; } }
	&__text {
		color: $text3;
		@include d {
			max-width: 320px;
			font-size: 16px; } }
	&__btns {
		margin-top: 94px;
		text-align: center;
		@include t {
			margin-top: 64px; } }
	&__btn {
		@include m {
			min-width: 100%;
			padding: 0 16px; } } }

@include dark {
	.quality4 {
		&__item {
			background: $dark3; } } }
