.advantages1 {
	@include m {
		padding: 80px 0 85px; }
	&__row {
		position: relative;
		display: flex;
		align-items: center;
		min-height: 1103px;
		padding: 100px 0;
		@include x {
			min-height: 1040px; }
		@include d {
			min-height: 840px; }
		@include m {
			display: block;
			min-height: auto;
			padding: 0;
			@include nl {
				margin-bottom: 90px; } } }
	&__wrap {
		position: relative;
		z-index: 3;
		@include m {
			margin-top: -10px; } }
	&__row:first-child &__wrap {
		max-width: 530px;
		margin-left: auto;
		@include d {
			max-width: 360px; }
		@include m {
			max-width: 100%; } }
	&__row:first-child &__preview {
		top: 50%;
		left: calc(50% - 657px);
		transform: translateY(-43%);
		@include x {
			left: calc(50% - 660px); }
		@include d {
			left: calc(50% - 440px);
			width: 430px; }
		@include t {
			left: calc(50% - 400px);
			width: 370px; }
		@include m {
			transform: translateY(0);
			margin: 0 auto; }
		@include a {
			width: auto;
			margin: 0 -64px; } }
	&__row:nth-child(2) &__wrap {
		max-width: 527px;
		@include d {
			max-width: 360px; } }
	&__row:nth-child(2) &__preview {
		top: 53%;
		right: calc(50% - 650px);
		transform: translateY(-45%);
		@include x {
			right: calc(50% - 660px); }
		@include d {
			right: calc(50% - 480px);
			width: 480px; }
		@include t {
			right: calc(50% - 460px);
			width: 430px; }
		@include m {
			margin: 0 auto;
			transform: translateY(0); }
		@include a {
			width: auto;
			margin: 0 -64px; } }
	&__title {
		margin-bottom: 33px;
		@include m {
			margin-bottom: 24px; } }
	&__info {
		margin-bottom: 36px; }
	&__item {
		display: flex;
		@include nl {
			margin-bottom: 33px; } }
	&__icon {
		display: flex;
		justify-content: center;
		align-items: center;
		flex-shrink: 0;
		width: 74px;
		height: 74px;
		margin-right: 32px;
		border-radius: 50%;
		@include d {
			margin-right: 20px; } }
	&__category {
		margin-bottom: 23px;
		@include h8;
		font-weight: 600;
		@include d {
			margin-bottom: 16px;
			font-size: 20px;
			line-height: 1.6; } }
	&__text {
		line-height: 2;
		color: $text3;
		@include d {
			font-size: 16px; } }
	&__preview {
		position: absolute;
		z-index: -1;
		@include m {
			position: static; }
		img {
			@include d {
				width: 100%; } } } }
